import React, {Component} from "react";
import notice from '../data/notice.json'
import '../App.css'

class Notice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            heading : "",
            message : "",
            signature : ""
        };
    }

    async componentDidMount() {
        //Loading json data locally for dev
        //console.log(notice)
        // const heading = notice.notice.heading;
        // const message = notice.notice.message;
        // const signature = notice.notice.signature;
        // this.setState({
        //     heading, message, signature
        // })
        //console.log(heading)

        //Loading json data from server for prod
        const response = await fetch('https://raw.githubusercontent.com/anchitmulye/database/master/anchitmulye/notice.json');
        const notice_data = await response.json();
        this.setState({
            heading: notice_data.notice.heading,
            message: notice_data.notice.message,
            signature: notice_data.notice.signature
        })
    }

    render() {
        return (
            <React.Fragment>
            <div>
                <div className="Notice-container">
                    <h1>{this.state.heading}</h1>
                    <p>{this.state.message}</p>
                    <p>{this.state.signature}</p>
                </div>
            </div>
            </React.Fragment>
        );
    }
}

export default Notice;